.skills {
  height: 100vh;
  background-color: darkviolet;
  display: flex;
  justify-content: center;
}

.container{
  width: 80%;
  border: 1px solid; 
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;

}

.skillsBlock{
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  align-items: center;
}

.skill{
  border: 1px solid black;
  width: 30%;
  height: 80%;
  justify-content: center;
}

.photo{
  height: 300px;
  width: 200px;
  background-color: lime;
}

