.projects  {
  height: 100vh;
  background-color: navy;
  display: flex;
  justify-content: center;
}

.container{
  width: 80%;
  border: 1px solid; 
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.header{
  border: 1px solid black;
  width: 100%;
  height: 200px;
  justify-content: center;
}

.projectsBlock{
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  align-items: center;
}

.projectsItem{
  border: 1px solid black;
  width: 40%;
  height: 80%;
  justify-content: space-evenly;
}

.photo{
  height: 300px;
  width: 200px;
  background-color: lime;
}