.contacts  {
  height: 100vh;
  background-color: orchid;
  display: flex;
  justify-content: center;
}

.container{
  width: 80%;
  border: 1px solid; 
  display: flex;
  justify-content: space-around;
  align-items: center;
}

textarea{
    height: 40%;
    resize: none;
}

.contactsBlock{
  display: flex;
  justify-content: space-evenly;
  width: 60%;
  height: 80%;
  align-items: stretch;
  flex-direction: column;
}

button{
  width: 50%;
  height: 50px;
}